<template>
  <div class="gallery-wrap">
    <!-- 3Ïó¥ Í∑∏Î¶¨Îìú Í∞§Îü¨Î¶¨ -->
    <div class="gallery-grid">
      <div
        v-for="(src, i) in visibleImages"
        :key="i"
        class="gallery-cell"
        @click="openLightbox(i)"
      >
        <img :src="src" :alt="`Ïõ®Îî© ÏÇ¨ÏßÑ ${i + 1}`" loading="lazy" />
      </div>
    </div>

    <!-- Îçî Î≥¥Í∏∞ Î≤ÑÌäº: Ï≤òÏùåÏóî 6Ïû•Îßå, ÌÅ¥Î¶≠ÌïòÎ©¥ Ï†ÑÏ≤¥ ÌëúÏãú -->
    <button v-if="!showAll" class="more-btn" @click="showAll = true">
      Îçî Î≥¥Í∏∞ <span class="more-arrow">‚à®</span>
    </button>

    <!-- ÎùºÏù¥Ìä∏Î∞ïÏä§ (Ï†ÑÏ≤¥ÌôîÎ©¥ Î∑∞Ïñ¥) -->
    <transition name="lb-fade">
      <div v-if="lightboxOpen" class="lightbox" @click.self="closeLightbox">
        <button class="lb-close" @click="closeLightbox">‚úï</button>
        <button class="lb-prev" @click="prev">‚Äπ</button>

        <div class="lb-img-wrap"
             @touchstart="handleTouchStart"
             @touchend="handleTouchEnd"
        >
          <img :src="images[currentIdx]" :alt="`Ïõ®Îî© ÏÇ¨ÏßÑ ${currentIdx + 1}`" />
          <p class="lb-counter">{{ currentIdx + 1 }} / {{ images.length }}</p>

          <button class="lb-next" @click="next">‚Ä∫</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'WeddingGallery',
  data () {
    return {
      showAll: false,
      lightboxOpen: false,
      currentIdx: 1,
      touchStartX: 0,
      touchEndX: 0,
      scrollY: 0,
      images: [
        require('@/assets/photo/1.jpg'),
        require('@/assets/photo/2.jpg'),
        require('@/assets/photo/3.jpg'),
        require('@/assets/photo/4.jpg'),
        require('@/assets/photo/5.jpg'),
        require('@/assets/photo/6.jpg'),
        require('@/assets/photo/7.jpg'),
        require('@/assets/photo/8.jpg'),
        require('@/assets/photo/9.jpg'),
        require('@/assets/photo/10.jpg'),
        require('@/assets/photo/11.jpg'),
        require('@/assets/photo/12.jpg'),
        require('@/assets/photo/13.jpg'),
        require('@/assets/photo/14.jpg'),
        require('@/assets/photo/15.jpg'),
        require('@/assets/photo/16.jpg'),
        require('@/assets/photo/17.jpg'),
        require('@/assets/photo/18.jpg'),
        require('@/assets/photo/19.jpg'),
        require('@/assets/photo/20.jpg')
      ]
    }
  },
  computed: {
    // showAllÏù¥ falseÎ©¥ Ï≤òÏùå 6Ïû•Îßå, trueÎ©¥ Ï†ÑÎ∂Ä ÌëúÏãú
    visibleImages () {
      return this.showAll ? this.images : this.images.slice(0, 9)
    }
  },
  methods: {
    openLightbox (idx) {
      // 'Îçî Î≥¥Í∏∞' Ï†Ñ Îã®Í≥ÑÏóêÏÑú ÌÅ¥Î¶≠Ìïú Í≤ΩÏö∞ Ïã§Ï†ú Ïù¥ÎØ∏ÏßÄ Ïù∏Îç±Ïä§ÏôÄ ÎèôÏùº
      this.currentIdx = idx
      this.lightboxOpen = true
      document.body.style.overflow = 'hidden'

      // ÌòÑÏû¨ Ïä§ÌÅ¨Î°§ ÏúÑÏπò Ï†ÄÏû•
      this.scrollY = window.scrollY

      document.body.style.position = 'fixed'
      document.body.style.top = `-${this.scrollY}px`
      document.body.style.width = '100%'
    },
    closeLightbox () {
      this.lightboxOpen = false

      document.body.style.position = ''
      document.body.style.top = ''
      document.body.style.width = ''

      // ÏõêÎûò Ïä§ÌÅ¨Î°§ ÏúÑÏπò Î≥µÏõê
      window.scrollTo(0, this.scrollY)
    },
    prev () {
      this.currentIdx = (this.currentIdx - 1 + this.images.length) % this.images.length
    },
    next () {
      this.currentIdx = (this.currentIdx + 1) % this.images.length
    },
    handleTouchStart (e) {
      this.touchStartX = e.changedTouches[0].screenX
    },
    handleTouchEnd (e) {
      this.touchEndX = e.changedTouches[0].screenX
      this.handleSwipe()
    },
    handleSwipe () {
      const diff = this.touchEndX - this.touchStartX

      // ÏµúÏÜå Ïù¥Îèô Í±∞Î¶¨ (ÎÑàÎ¨¥ ÎØºÍ∞êÌïòÏßÄ ÏïäÍ≤å)
      if (Math.abs(diff) < 50) return

      if (diff > 0) {
        // Ïò§Î•∏Ï™ΩÏúºÎ°ú Î∞ÄÏóàÏúºÎ©¥ Ïù¥Ï†Ñ ÏÇ¨ÏßÑ
        this.prev()
      } else {
        // ÏôºÏ™ΩÏúºÎ°ú Î∞ÄÏóàÏúºÎ©¥ Îã§Ïùå ÏÇ¨ÏßÑ
        this.next()
      }
    }
  }
}
</script>

<style scoped>
/* ============================================================
   Í∞§Îü¨Î¶¨ Í∑∏Î¶¨Îìú (3Ïó¥, Í∞ÑÍ≤© 2px, Ï∞∏Í≥† Ïù¥ÎØ∏ÏßÄ Ïä§ÌÉÄÏùº)
   ============================================================ */
