  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.9);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lb-img-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90vw;
  touch-action: pan-y;
}

