<template>

  <section class="guest-book">

    <!-- üî• 2Í∞úÎßå ÌëúÏãú -->
    <div class="message-list">
      <div
        v-for="post in posts.slice(0,2)"
        :key="post.id"
        class="message-card"
      >
        <p class="writer">{{ post.name }}</p>
        <p class="content">{{ post.message }}</p>
      </div>
    </div>

    <!-- Ï†ÑÏ≤¥ Î¶¨Ïä§Ìä∏ Î™®Îã¨ -->
    <div v-if="openListModal" class="modal-overlay" @click.self="openListModal = false">
      <div class="modal-box large">
        <h5>Ï†ÑÏ≤¥ Î∞©Î™ÖÎ°ù</h5>
        <p class="guestbook-answer">
          ÌñâÎ≥µÌïòÍ≤å ÏûòÏÇ¥Í≤†ÏäµÎãàÎã§ ‚ô•Ô∏é  Í∞êÏÇ¨Ìï©ÎãàÎã§.<br>

          ÌòúÎ∂Ñ Ïö∞ÏÑ≠ Ïò¨Î¶º üë∞üèª‚Äç‚ôÄÔ∏èü§µüèª
        </p>

        <div class="scroll-box">
          <div
            v-for="post in posts"
            :key="post.id"
            class="message-card"
          >
            <p class="writer">{{ post.name }}</p>
            <p class="content">{{ post.message }}</p>
          </div>
        </div>

        <button class="submit-btn" @click="openListModal = false">
          Îã´Í∏∞
        </button>
      </div>
    </div>

    <!-- Î™®Îã¨ -->
    <div v-if="openModal" class="modal-overlay" @click.self="closeModal">
      <div class="modal-box">
        <h3>Ï∂ïÌïò Î©îÏãúÏßÄ ÎÇ®Í∏∞Í∏∞</h3>

        <input
          v-model="name"
          type="text"
          placeholder="Ïù¥Î¶Ñ"
        />

        <textarea
          v-model="message"
          rows="4"
          placeholder="Î©îÏãúÏßÄÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
        />

        <div class="modal-buttons">
          <button class="cancel-btn" @click="closeModal">Ï∑®ÏÜå</button>
          <button class="submit-btn" @click="submitPost">Ï†ÑÏÜ°</button>
        </div>
      </div>
    </div>

    <!-- ÎçîÎ≥¥Í∏∞ Î≤ÑÌäº -->
    <button
      v-if="posts.length > 2"
      class="more-btn"
      @click="openListModal = true"
    >
      Ï†ÑÏ≤¥Î≥¥Í∏∞
    </button>

    <!-- Í∏ÄÏì∞Í∏∞ Î≤ÑÌäº -->
    <button class="write-btn" @click="openModal = true">
      <img :src="require('@/assets/icon/write.png')" alt="write" />
    </button>

  </section>
</template>

<script>
import { ref, onMounted } from 'vue'
import { db } from '@/store/firebase'
import {
  collection,
  addDoc,
  query,
  orderBy,
  onSnapshot
} from 'firebase/firestore'

export default {
  name: 'WeddingGuestBook',
  setup () {
    const name = ref('')
    const message = ref('')
    const posts = ref([])
    const openModal = ref(false)
    const openListModal = ref(false)

    const closeModal = () => {
      openModal.value = false
      name.value = ''
      message.value = ''
    }

    const submitPost = async () => {
      if (!name.value || !message.value) {
        alert('Ïù¥Î¶ÑÍ≥º Î©îÏãúÏßÄÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
        return
      }

      await addDoc(collection(db, 'guestBook'), {
        name: name.value,
        message: message.value,
        createdAt: new Date()
      })

      closeModal()
    }

    onMounted(() => {
      const q = query(
        collection(db, 'guestBook'),
        orderBy('createdAt', 'desc')
      )

      // üî• Ïã§ÏãúÍ∞Ñ Î∞òÏòÅ
      onSnapshot(q, (snapshot) => {
        posts.value = snapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }))
      })
    })

    return {
      name,
      message,
      posts,
      openModal,
      openListModal,
      closeModal,
      submitPost
    }
  }
}
</script>

