<script>
import dayjs from 'dayjs'
import KakaoMap from '@/components/KakaoMap.vue'
import MusicPlayer from '@/components/MusicPlayer.vue'
import WeddingCalendar from '@/components/WeddingCalendar.vue'
import WeddingGallery from '@/components/WeddingGallery.vue'
import WeddingPanel from '@/components/WeddingPanel.vue'
import WeddingGuestBook from '@/components/WeddingGuestBook.vue'

export default {
  name: 'IndexPage',
  data () {
    return {
      dDay: '',
      location: 'Í≤ΩÍ∏∞ ÏàòÏõêÏãú ÌåîÎã¨Íµ¨ Í∂åÍ¥ëÎ°ú 134Î≤àÍ∏∏ 46',
      scrollTimeout: null
    }
  },
  created () {
    this.setDDay()
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener('scroll', this.handleScroll)
    })
  },
  beforeUnmount () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    // Í≤∞ÌòºÏãù(2026-06-20) Í∏∞Ï§Ä D-Day Í≥ÑÏÇ∞
    setDDay () {
      const now = dayjs()
      const weddingDay = dayjs('2026-06-20')
      const diff = Math.floor(now.diff(weddingDay, 'day', true))
      if (diff === 0) {
        this.dDay = 'D-DAY'
      } else if (diff > 0) {
        this.dDay = `D+${diff}`
      } else {
        this.dDay = `D${diff}`
      }
    },
    // Ïä§ÌÅ¨Î°§ Ï§ë ÏßÄÎèÑ ÌÑ∞Ïπò Ï∞®Îã® (Ïã§ÏàòÎ°ú ÏßÄÎèÑ ÎìúÎûòÍ∑∏ Î∞©ÏßÄ)
    handleScroll () {
      const mapComponent = this.$refs.map

      if (!mapComponent || !mapComponent.setPointerEvents) {
        return
      }

      window.clearTimeout(this.scrollTimeout)

      mapComponent.setPointerEvents('none')

      this.scrollTimeout = window.setTimeout(() => {
        mapComponent.setPointerEvents('')
      }, 300)
    },
    async handleCopy (text) {
      try {
        if (navigator.clipboard && window.isSecureContext) {
          // ÏµúÏã† Î∏åÎùºÏö∞Ï†Ä
          await navigator.clipboard.writeText(text)
        } else {
          // Î™®Î∞îÏùº Ïù∏Ïï±Î∏åÎùºÏö∞Ï†Ä ÎåÄÏùë fallback
          const textarea = document.createElement('textarea')
          textarea.value = text
          textarea.style.position = 'fixed'
          textarea.style.left = '-9999px'
          document.body.appendChild(textarea)
          textarea.focus()
          textarea.select()
          document.execCommand('copy')
          document.body.removeChild(textarea)
        }

        alert('Ï£ºÏÜåÍ∞Ä Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§.')
      } catch (err) {
        console.error(err)
        alert('Î≥µÏÇ¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
      }
    }
  },
  components: {
    KakaoMap,
    MusicPlayer,
    WeddingCalendar,
    WeddingGallery,
    WeddingPanel,
    WeddingGuestBook
  }
}
</script>

