<template>
  <!-- ÌôîÎ©¥ Ïö∞Ï∏° ÌïòÎã®Ïóê Í≥†Ï†ïÎêú ÌîåÎ°úÌåÖ ÏùåÏïÖ ÌîåÎ†àÏù¥Ïñ¥ -->
  <div class="music-player">

    <!-- HTML5 Ïò§ÎîîÏò§ (loop: Î∞òÎ≥µ Ïû¨ÏÉù) -->
    <audio
      ref="audio"
      :src="musicSrc"
      loop
      preload="none"
    />

    <!-- Ïû¨ÏÉù/Ï†ïÏßÄ ÌîåÎ°úÌåÖ Î≤ÑÌäº -->
    <button
      class="music-btn"
      :class="{ 'is-playing': isPlaying }"
      aria-label="Î∞∞Í≤ΩÏùåÏïÖ Ïû¨ÏÉù/Ï†ïÏßÄ"
      @click="togglePlay"
    >
      <!-- ÌöåÏ†ÑÌïòÎäî ÏùåÌëú ÏïÑÏù¥ÏΩò -->
      <span class="music-icon">‚ô™</span>
    </button>

    <!-- ÌòÑÏû¨ ÏÉÅÌÉú Ìà¥ÌåÅ ÎùºÎ≤® -->
    <span class="music-label">{{ isPlaying ? 'ÏùåÏïÖ ÏºúÏßê' : 'ÏùåÏïÖ Í∫ºÏßê' }}</span>
  </div>
</template>

<script>
export default {
  name: 'MusicPlayer',
  props: {
    /**
     * Ïû¨ÏÉùÌï† ÏùåÏïÖ ÌååÏùºÏùò URL ÎòêÎäî Í≤ΩÎ°ú
     * - ÏßÅÏ†ë ÌååÏùºÏùÑ Ï†úÍ≥µÌïòÎ†§Î©¥: require('@/static/music/bgm.mp3')
     * - Ïô∏Î∂Ä URLÎèÑ ÏÇ¨Ïö© Í∞ÄÎä• (CORS ÌóàÏö© ÌïÑÏöî)
     */
    musicSrc: {
      type: String,
      // Î¨¥Î£å ÌîºÏïÑÎÖ∏ BGM (Í≥µÍ∞ú ÎèÑÎ©îÏù∏ ÏòàÏãú URL)
      // Ïã§Ï†ú ÏÑúÎπÑÏä§ Ïãú static/music/ Ìè¥ÎçîÏóê mp3 ÌååÏùºÏùÑ ÎÑ£Ïñ¥ Í≤ΩÎ°úÎ•º Î≥ÄÍ≤ΩÌïòÏÑ∏Ïöî.
      default: process.env.BASE_URL + 'bgm-wedding.mp3'
    }
  },
  data () {
    return {
      isPlaying: false
    }
  },
  mounted () {
    const startMusic = () => {
      const audio = this.$refs.audio

      if (!audio) return// ‚úÖ null Î∞©Ïñ¥ ÏΩîÎìú Ï∂îÍ∞Ä

      if (!this.isPlaying) {
        audio.play().then(() => {
          this.isPlaying = true
        }).catch(() => {})
      }

      document.removeEventListener('click', startMusic)
      document.removeEventListener('touchstart', startMusic)
    }

    document.addEventListener('click', startMusic)
    document.addEventListener('touchstart', startMusic)
  },
  methods: {
    /**
     * Î∞∞Í≤ΩÏùåÏïÖ Ïû¨ÏÉù/Ï†ïÏßÄ ÌÜ†Í∏Ä
     * - Î∏åÎùºÏö∞Ï†Ä Ï†ïÏ±ÖÏÉÅ ÏµúÏ¥à Ïû¨ÏÉùÏùÄ Î∞òÎìúÏãú ÏÇ¨Ïö©Ïûê ÏÉÅÌò∏ÏûëÏö© ÌõÑÏóêÎßå Í∞ÄÎä•
     */
    async togglePlay () {
      const audio = this.$refs.audio

      if (this.isPlaying) {
        audio.pause()
        this.isPlaying = false
      } else {
        try {
          await audio.play()
          this.isPlaying = true
        } catch (err) {
          // ÏûêÎèôÏû¨ÏÉù Ï†ïÏ±Ö(AutoPlay Policy)ÏúºÎ°ú Ïã§Ìå®Ìï† Í≤ΩÏö∞ ÏΩòÏÜîÏóêÎßå ÌëúÏãú
          console.warn('ÏùåÏïÖ Ïû¨ÏÉù Ïã§Ìå®:', err.message)
        }
      }
    }
  }
}
</script>

<style scoped>
/* ÌôîÎ©¥ Ïö∞Ï∏° ÏÉÅÎã® Í≥†Ï†ï Ïª®ÌÖåÏù¥ÎÑà */
.music-player {
  position: fixed;
  top: 28px;
  right: 20px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

/* ÏõêÌòï ÌîåÎ°úÌåÖ Î≤ÑÌäº */
.music-btn {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(64deg, #f4a8b6, #ffffff);
  color: #fff;
  font-size: 22px;
  cursor: pointer;
  /* ÌäÄÏñ¥ÎÇòÏò® ÏûÖÏ≤¥Í∞ê ÏÑÄÎèÑ */
  box-shadow: 0 4px 16px rgba(139, 94, 82, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s, box-shadow 0.2s;
}

.music-btn:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(139, 94, 82, 0.55);
}

/* ÏùåÌëú ÏïÑÏù¥ÏΩò: Ïû¨ÏÉùÏ§ëÏùº Îïå ÌöåÏ†Ñ */
.music-icon {
  display: inline-block;
  transition: transform 0.3s;
}

.music-btn.is-playing .music-icon {
  animation: spin 3s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg);   }
  to   { transform: rotate(360deg); }
}

/* ÏÉÅÌÉú ÎùºÎ≤® */
.music-label {
  font-size: 10px;
  letter-spacing: 0.5px;
  text-align: center;
  white-space: nowrap;
  /* Î∞∞Í≤ΩÏùÑ ÏÇ¥Ïßù Î∂àÌà¨Î™ÖÌïòÍ≤å (Í∞ÄÎèÖÏÑ± ÌôïÎ≥¥) */
  border-radius: 999px;
  padding: 2px 8px;
}
</style>
